<template>
  <div class="cart">
    <h4></h4>
    <div class="cart-main">
      <div class="cart-th">
        <div class="cart-th1"></div>
        <div class="cart-th2"></div>
        <div class="cart-th3"></div>
        <div class="cart-th4"></div>
        <div class="cart-th5"></div>
        <div class="cart-th6"></div>
      </div>
      <div class="cart-body" v-show="cartInfoList.length">
				<!--  -->
        <ul class="cart-list" v-for="cartInfo in cartInfoList" :key="cartInfo.id">
					<!--  -->
          <li class="cart-list-con1">
            <input 
							type="checkbox" 
							name="chk_list" 
							:checked="cartInfo.isChecked"
							@click.prevent="handleCheckOne(cartInfo)"
						>
          </li>
					<!-- + -->
          <li class="cart-list-con2">
            <img :src="cartInfo.imgUrl">
            <div class="item-msg">{{cartInfo.skuName}}</div>
          </li>
					<!--  -->
          <li class="cart-list-con4">
            <span class="price">{{cartInfo.cartPrice}}</span>
          </li>
					<!--  -->
          <li class="cart-list-con5">

						<!--  -->
            <a class="mins" @click="handleChangeGoodNum('decrement',cartInfo)">-</a>
						<!--  -->
            <input 
							type="text" 
							class="itxt" 
							:value="cartInfo.skuNum" 
							@change="handleChangeGoodNum('input',cartInfo,$event)"
						>
						<!--  -->
            <a class="plus" @click="handleChangeGoodNum('increment',cartInfo)">+</a>


          </li>
					<!--  -->
          <li class="cart-list-con6">
            <span class="sum">{{cartInfo.cartPrice * cartInfo.skuNum}}</span>
          </li>
          <li class="cart-list-con7">
            <a class="sindelet" @click="handleDeleteOne(cartInfo.skuId)"></a>
            <br>
          </li>
        </ul>
      </div>
    </div>
		<!--  -->
    <div class="cart-tool" v-show="cartInfoList.length">
      <div class="select-all">
        <input 
					class="chooseAll" 
					type="checkbox" 
					:checked="isAll"
					@click.prevent="handleCheckAll"
				>
        <span></span>
      </div>
      <div class="option">
        <a @click="handleBatchDelete"></a>
      </div>
      <div class="money-box">
        <div class="chosed">
          <span>{{total}}</span></div>
        <div class="sumprice">
          <em> </em>
          <i class="summoney">{{totalPrice}}</i>
        </div>
        <div class="sumbtn">
          <router-link class="sum-btn" to="/trade"></router-link>
        </div>
      </div>
    </div>
		<!--  -->
		<div class="empty" v-show="!cartInfoList.length">
			<h2></h2>
			<img src="http://49.232.112.44/img/empty.751bed26.gif" alt="">
		</div>
  </div>
</template>

<script>
	import {
		reqCartList,
		reqCheckOneCart,
		reqCheckAllCart,
		reqDeleteOneCart,
		reqBatchDeleteCart,
		reqAddGood2Cart
	} from '@/api'
	
	import {goodNumReg} from '@/utils/reg'
	import _ from 'lodash'

  export default {
    name: 'Cart',
		data(){
			return {
				cartInfoList:[],
				isExecute:true
			}
		},
		computed:{
			// 
			isAll(){
				// isChecked1isAlltruefalse
				return this.cartInfoList.every( cartInfo => cartInfo.isChecked === 1)
			},
			// 
			total(){
				let n = 0 //
				this.cartInfoList.forEach( cartInfo => {
					if(cartInfo.isChecked){
						n += cartInfo.skuNum
					}
				})
				return n
			},
			// 
			totalPrice(){
				let n = 0 //
				this.cartInfoList.forEach( cartInfo => {
					if(cartInfo.isChecked){
						n += cartInfo.skuNum * cartInfo.cartPrice
					}
				})
				return n
			}
		},
		methods:{
			//
			async getCartList(){
				// 
				let result = await reqCartList()
				if(result.code === 200){
					// 
					if(result.data[0]){
						this.cartInfoList = result.data[0].cartInfoList
					}
				}else{
					alert(result.message)
				}
			},
			// 
			async handleCheckOne(cartInfo){
				// id
				let {skuId,isChecked} = cartInfo
				// 
				isChecked = (isChecked === 1 ? 0 : 1)
				//
				let result = await reqCheckOneCart(skuId,isChecked)
				if(result.code === 200){
					// 
					cartInfo.isChecked = isChecked
				}else{
					// 
					alert(result.message)
				}
			},
			// 
			async handleCheckAll(){
				// 
				let {cartInfoList,isAll} = this
				// 
				let isChecked = (isAll ? 0 : 1)
				// id
				let idList = []
				// id
				cartInfoList.forEach( cartInfo => idList.push(cartInfo.skuId))
				// 
				let result = await reqCheckAllCart(idList,isChecked)
				// 
				if(result.code === 200){
					// 
					cartInfoList.forEach( cartInfo => cartInfo.isChecked = isChecked)
				}else{
					// 
					alert(result.message)
				}
			},
			// 
			async handleDeleteOne(id){
				// 
				if(confirm('')){
					// 
					const result = await reqDeleteOneCart(id)
					// 
					if(result.code === 200){
						// 

						// filter
						// this.cartInfoList = this.cartInfoList.filter(cartInfo => cartInfo.skuId !== id)
						
						// findIndex
						let index = this.cartInfoList.findIndex(cartInfo => cartInfo.skuId === id)
						this.cartInfoList.splice(index,1)
					}else{
						// 
						alert(result.message)
					}
				}
			},
			// 
			async handleBatchDelete(){
				if(confirm('')){
					// skuId
					const idList = []
					// skuId
					this.cartInfoList.forEach( cartInfo => {
						if(cartInfo.isChecked) {
							idList.push(cartInfo.skuId)
						} 
					})
					// 
					let result = await reqBatchDeleteCart(idList)
					// 
					if(result.code === 200){
						// 
						// isChecked0
						this.cartInfoList = this.cartInfoList.filter( cartInfo => !cartInfo.isChecked)
					}else{
						// 
						alert(result.message)
					}
				}
			},
			// 
			async handleChangeGoodNum(type,cartInfo,e){
				// 
				if(!this.isExecute) return
				// 
				this.isExecute = false
				//switchisExecutetrue
				switch (type) {
					// 
					case 'increment':
						if(cartInfo.skuNum === 200){
							alert('200')
						}
						else{
							// +1
							let result = await reqAddGood2Cart(cartInfo.skuId,1)
							// 
							if(result.code === 200){
								// 1
								cartInfo.skuNum++
								// 
								cartInfo.isChecked = 1
							}else{
								alert(result.message)
							}
						}
						break;
					// 
					case 'decrement':
						if(cartInfo.skuNum === 1){
							alert('1')
						}
						else{
							// +1
							let result = await reqAddGood2Cart(cartInfo.skuId,-1)
							// 
							if(result.code === 200){
								// 1
								cartInfo.skuNum--
								// 
								cartInfo.isChecked = 1
							}else{
								alert(result.message)
							}
						}
						break;
					// 
					case 'input':
						// 
						const {value} = e.target
						// skuId
						const {skuId,skuNum} = cartInfo
						// 
						// 
						if(goodNumReg.test(value)){
							// 
							// 
							const disNum = value - skuNum
							// 
							let result = await reqAddGood2Cart(skuId,disNum)
							// 
							if(result.code === 200){
								// 
								cartInfo.skuNum = value * 1
								// 
								cartInfo.isChecked = 1
							}else{
								// 
								alert(result.message)
								// DOM
								e.target.value = skuNum
							}
						}
						// 
						else if(value > 200){
							// 
							const disNum = 200 - skuNum
							// 0DOM200
							if(disNum === 0) {
								this.isExecute = true
								return e.target.value = 200
							}
							// 
							const result = await reqAddGood2Cart(skuId,disNum)
							// 
							if(result.code === 200){
								// DOM200
								cartInfo.skuNum = e.target.value = 200
								// 
								cartInfo.isChecked = 1
							}else{
								// 
								alert(result.message)
								// DOM
								e.target.value = skuNum
							}
						}
						// @-912.8
						else{
							// 
							e.target.value = skuNum
						}
				}
				// 
				this.isExecute = true
			}
		},
		mounted(){
			// getCartList
			this.getCartList()
		}
  }
</script>

<style lang="less" scoped>
.cart {
  width: 1200px;
  margin: 0 auto;
  h4 {
    margin: 9px 0;
    font-size: 14px;
    line-height: 21px;
  }
  .cart-main {
    .cart-th {
      background: #f5f5f5;
      border: 1px solid #ddd;
      padding: 10px;
      overflow: hidden;
      & > div {
        float: left;
      }
      .cart-th1 {
        width: 25%;
        input {
          vertical-align: middle;
        }
        span {
          vertical-align: middle;
        }
      }
      .cart-th2 {
        width: 25%;
      }
      .cart-th3,
      .cart-th4,
      .cart-th5,
      .cart-th6 {
        width: 12.5%;
      }
    }
    .cart-body {
      margin: 15px 0;
      border: 1px solid #ddd;
      .cart-list {
        padding: 10px;
        border-bottom: 1px solid #ddd;
        overflow: hidden;
        & > li {
          float: left;
        }
        .cart-list-con1 {
          width: 15%;
        }
        .cart-list-con2 {
          width: 35%;
          img {
            width: 82px;
            height: 82px;
            float: left;
          }
          .item-msg {
            float: left;
            width: 150px;
            margin: 0 10px;
            line-height: 18px;
          }
        }
        .cart-list-con4 {
          width: 10%;
        }
        .cart-list-con5 {
          width: 17%;
          .mins {
            border: 1px solid #ddd;
            border-right: 0;
            float: left;
            color: #666;
            width: 6px;
            text-align: center;
            padding: 8px;
						text-decoration: none;
          }
          input {
            border: 1px solid #ddd;
            width: 40px;
            height: 31px;
            float: left;
            text-align: center;
            font-size: 14px;
          }

          .plus {
            border: 1px solid #ddd;
            border-left: 0;
            float: left;
            color: #666;
            width: 6px;
            text-align: center;
            padding: 8px;
						text-decoration: none;
          }
        }

        .cart-list-con6 {
          width: 10%;

          .sum {
            font-size: 16px;
          }
        }

        .cart-list-con7 {
          width: 13%;

          a {
            color: #666;
          }
        }
      }
    }
  }
  .cart-tool {
    overflow: hidden;
    border: 1px solid #ddd;

    .select-all {
      padding: 10px;
      overflow: hidden;
      float: left;

      span {
        vertical-align: middle;
      }

      input {
        vertical-align: middle;
      }
    }

    .option {
      padding: 10px;
      overflow: hidden;
      float: left;

      a {
        float: left;
        padding: 0 10px;
        color: #666;
      }
    }

    .money-box {
      float: right;

      .chosed {
        line-height: 26px;
        float: left;
        padding: 0 10px;
      }

      .sumprice {
        width: 200px;
        line-height: 22px;
        float: left;
        padding: 0 10px;

        .summoney {
          color: #c81623;
          font-size: 16px;
        }
      }
			

      .sumbtn {
        float: right;
        a {
          display: block;
          position: relative;
          width: 96px;
          height: 52px;
          line-height: 52px;
          color: #fff;
          text-align: center;
          font-size: 18px;
          font-family: "Microsoft YaHei";
          background: #e1251b;
          overflow: hidden;
					text-decoration: none;
					&hover {
						color: white !important;
					}
        }
      }
    }
  }
	.empty{
		margin-top: 20px;
		text-align: center;
		h2{
			color: gray;
			text-align: center;
		}
		img{
			width: 300px;
		}
	}
}
</style>